<template>
	<view class="news">
		<view class="list boxsiz column">
			<template v-for="(item, index) in newsList">
				<navigator :url="'/pages/news/detail?id='+item.id" class="box row bothSide verCenter" hover-class="none">
					<view class="text">
						<text class="t1">{{ item.title }}</text>
						<rich-text class="t2">{{ item.ctime }}</rich-text>
					</view>
					<view class="pic"><image lazy-load="true" fade-show="true" :src="item.img" mode="aspectFill"></image></view>
				</navigator>
			</template>
		</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';
export default {
	data() {
		return {
			newsList: []
		};
	},
	onLoad(options) {
		this.getData();
	},
	methods: {
		getData() {
			this.request(API.getNewsList,'GET',{},true).then(res => {
				if (res.code === 10000) {
					this.newsList = res.data;
				}
			});
		}
	}
};
</script>

<style scoped lang="scss">
@import '../../assets/css/news/index.scss';
</style>
